#root {
  height: 100%;
}

.App {
  height: 100%;
}

.main {
  height: 100%;
  display: flex;
}

.header {
  &__in {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;

    .logo {
      width: 71px;
      height: 50px;
      background: url("../logo.svg") no-repeat 0 0;
    }
  }
}

.abs-loader {
  position: absolute;
  z-index: 100;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
  background: rgba(255, 255, 255, .3);
}

.full-width {
  width: 100%;
}

.dropdown-menu {
  background: #fff;
  border-radius: 3px;
  padding: 10px 5px;
  border: 1px solid #ddd;
  width: 150px;

  &-title {
    font-size: 19px;
    display: block;
    padding: 0 0 10px;
  }

  button {
    width: 100%;
  }
}

.form-field-item {
  margin: 0 0 20px;

  &__title {
    display: flex;
    justify-content: space-between;
    margin: 0 0 8px;
    font-size: 14px;
    line-height: 16px;
    color: #65647A;
  }

  &__error {
    margin: 2px 0 0;
    color: #ea0505;
    font-size: 12px;

    .pincode-input-text {
      border-color: #ea0505 !important;
    }
  }

  &__note {
    margin: 3px 0 0;
    color: #B2B1BC;
    font-size: 11px;
  }
}

.custom-modal {
  .modal-loader {
    position: absolute;
    z-index: 100;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    background: rgba(255, 255, 255, .5);
  }

  .ant-modal-content {

    .ant-modal-close {
      top: 10px;
      right: 8px;

      .ant-modal-close-x {
        color: #97A6BA;
      }

      &:focus, &:hover {
        .ant-modal-close-x {
          color: darken(#97A6BA, 20);
        }
      }
    }
  }

  .ant-modal-header {
    background: #FBFDFE;
    border: none;
    padding: 24px 24px 20px;
  }

  .ant-modal-body {
    padding: 10px 24px 30px;
  }

  &__button-row {
    margin-top: 30px;

    .ant-btn {
      line-height: 1;
    }
  }
}

.ant-layout-sider-children {
  background: #fff;
}

/* ------------------------*/

.add-category-btn {
  text-align: center;
  padding: 15px 0 0;
}

.category-page {
  position: relative;

  &__head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 0 20px;

    h1 {
      margin: 0;
      font-size: 24px;
    }
  }
}

.words {
  background: #fafafa;

  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #ddd;
    font-size: 18px;

    &__word {
      position: relative;
      padding: 10px 25px;
      box-sizing: border-box;
      width: 50%;

      &:first-child {
        border-right: 1px solid #ddd;
      }
    }
  }
}